<template>
  <div class="add">
      <el-form :model="formData" label-width="80px">
          <el-form-item label="上传图片">
              <el-upload
                class="avatar-uploader"
                action="https://upload-z1.qiniup.com"
                :show-file-list="false"
                :on-success="upload"
                :data="{token:token}">
                <img v-if="formData.img" :src="formData.img" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
          </el-form-item>
          <el-form-item>
              <el-button @click="upData" v-if="$route.name == '轮播图添加页'">上传</el-button>
          </el-form-item>
          <el-form-item>
              <el-button @click="saveEdit" v-if="$route.name == '轮播图修改页'">保存修改</el-button>
          </el-form-item>
      </el-form>
  </div>
</template>
<script>

export default {
  data() {
      return {
          formData:{
              img:""
          },
          token:''
      }
  },
  methods:{
      upload(res) {
        //   console.log(res)
          this.formData.img = "http://image.yaojunrong.com/" + res.key
      },
      getToken() {
          this.$axios.qiniuGet().then(res => {
            //   console.log(res)
            this.token = res.data
          })
      },
      upData() {
          this.$axios.post('addSwiper',{img:this.formData.img}).then(res => {
              if(res.code == 200){
                  this.$message({
                    message: '图片上传成功！！！',
                    type: 'success'
                  });
                  setTimeout(() => {
                      this.$router.push('/swiper')
                  },1000)
              }
          })
      },
      getUpdateData() {
          let {id} = this.$route.query;
          this.$axios.get('getSwiper',{id}).then(res => {
              if(res.code == 200){
                  console.log(res)
                  this.formData = res.data[0]
              }
          })
      },
      saveEdit() {
          let {id} = this.$route.query;
          var img = this.formData.img;
          this.$axios.post('updateSwiper',{id,img}).then(res => {
              if(res.code == 200) {
                  this.$message({
                    message: '图片修改成功！！！',
                    type: 'success'
                  });
                  setTimeout(() => {
                      this.$router.push('/swiper')
                  },1000)
              }
          })
      }
  },
  created() {
      this.getToken()
      if(this.$route.name == '轮播图修改页'){
          this.getUpdateData()
      }
  },
  watch:{
      $route(to,from,next) {
          if(to.name == '轮播图添加页'){
              this.formData = {
                  img:''
              }
          }
      }
  }
}
</script>
<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>
<style scoped>
.add{
    margin-top:20px;
}
</style>



